<template>
    <div class="index">
        <div class="ant-layout">
            <div class="ant-layout ant-layout-has-sider">
                <div class="ant-layout" style="padding: 20px 0;background: #fff;">
                    <div style="width: 1200px;margin: 0 auto;background: #fff;">
                        <div style="margin-bottom: 10px;font-size: 16px;color: #56509F;">-</div>
                        <div class="margin-top-20 color-6" style="padding: 0 20px;background: #F6F6F6;border: 1px solid #EBEBEB;line-height: 50px;">
                            
                        </div>

                        <v-form direction="horizontal" class="margin-top-20">
                            <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
                                <span class="ant-form-text" id="userName" name="userName" style="font-size: 18px;">{{payData.name}}</span>
                            </v-form-item>
                            <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
                                <div class="inline-block" style="font-size: 28px;margin-right: 20px;"><span class="color-warn">{{payData.price}}</span></div>
                                <v-button type="default" html-type="button" @click="refreshPrice()"></v-button>
                            </v-form-item>
                            <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
                                <v-radio-group v-model="payStyle" style="margin-top: 17px;">
                                    <v-radio label="alipay">
                                        <svg class="icon inline-block" aria-hidden="true" style="font-size: 180px;margin-top: -60px;">
                                            <use xlink:href="#icon-zhifubao"></use>
                                        </svg>
                                    </v-radio>
                                    <v-radio label="wechat">
                                        <svg class="icon inline-block" aria-hidden="true" style="font-size: 180px;margin-top: -60px;">
                                            <use xlink:href="#icon-weixinzhifu"></use>
                                        </svg>
                                    </v-radio>
                                    <v-radio label="yhk">
                                        <svg class="icon inline-block" aria-hidden="true" style="font-size: 80px;margin-bottom: 40px;">
                                            <use xlink:href="#icon-weibiaoti-"></use>
                                        </svg>
                                        <span class="inline-block" style="vertical-align: top;margin-top: 11px;font-size: 24px;"></span>
                                    </v-radio>
                                </v-radio-group>
                            </v-form-item>

                            <v-form-item :wrapper-col="{span:22,offset:2}" style="margin-top:24px">
                                <a v-if='payStyle=="alipay"' class='ant-btn ant-btn-warning' target='_blank' :href='"http://aci-api.chaozhiedu.com/api/pay/alipay/"+payState.token'></a>
                                <a v-else class='ant-btn ant-btn-warning' @click="pay"></a>
                            </v-form-item>
                        </v-form>
                    </div>
                </div>
            </div>
        </div>

        <v-modal
            title=""
            :width="800"
            :visible="wechatPayDialog"
            @cancel="wechatPayDialogCancle"
            wrap-class-name="vertical-center-modal">
            <div class="text-center" style="font-size: 16px;">
                <p>{{payData.name}}<br/>
                    {{payData.price}} </p>
                <p><img :src='payResult.qrcode' style='width:200px;height:200px;' /></p>
                <p>
                    <svg class="icon inline-block" aria-hidden="true" style="font-size: 60px;margin-top: -20px;">
                        <use xlink:href="#icon-weixinzhifu"></use>
                    </svg>
                </p>
                <p style="font-size: 18px;">010-51657777</p>
            </div>
            <div slot="footer">
                <v-button key="cancel" type="ghost" size="large" @click="wechatPayDialogCancle"></v-button>
            </div>
        </v-modal>

        <v-modal
        title=""
        :width="800"
        :visible="yhkPayDialog"
        @cancel="yhkPayDialogCancle"
        wrap-class-name="vertical-center-modal">
        <div style="font-size: 16px;">
            <p style="font-weight: bold;"></p>
            <p><br/>
                <br/>
                899912828111288</p>
            <p>X<br/>
                010-51657777</p>
        </div>
        <div slot="footer">
            <v-button key="cancel" type="ghost" size="large" @click="yhkPayDialogCancle"></v-button>
        </div>
    </v-modal>

        <v-modal
            title=""
            :width="800"
            :visible="alipayPayDialog1"
            @cancel="alipayPayDialogCancle1"
            wrap-class-name="vertical-center-modal">
            <div class="clearfix" style="font-size: 16px;">
                <div class="pull-left margin-left-20">
                    <i class="iconfont icon-warning" style="font-size: 100px;color: #FF4000;"></i>
                </div>
                <div class="pull-left margin-left-20" style="width: 450px;">
                    <div style="font-size: 24px;">
                        <br/>
                        
                    </div>
                    <div class="margin-top-20" style="font-size: 14px;color: #FF4000;">
                        
                    </div>
                    <div class="margin-top-20">
                        <v-button type="primary"></v-button>
                        <v-button type="default" style="margin-left: 10px;"></v-button>
                    </div>
                    <div class="margin-top-20" style="font-size: 18px;">010-51657777</div>
                </div>
                <!-- <div v-html="payResult.form"></div> -->
            </div>
            <div slot="footer">
                <v-button key="cancel" type="ghost" size="large" @click="alipayPayDialogCancle1"></v-button>
            </div>
        </v-modal>

        <v-modal
            title=""
            :width="800"
            :visible="alipayPayDialog2"
            @cancel="alipayPayDialogCancle2"
            wrap-class-name="vertical-center-modal">
            <div class="clearfix" style="font-size: 16px;">
                <div class="pull-left margin-left-20">
                    <i class="iconfont icon-chenggong" style="font-size: 100px;color: #7ED321;"></i>
                </div>
                <div class="pull-left margin-left-20" style="width: 450px;">
                    <div style="font-size: 24px;">
                        
                    </div>
                    <div class="margin-top-20" style="font-size: 14px;">
                        {{payData.name}}
                    </div>
                    <div class="margin-top-20" style="font-size: 14px;color: #FF4000;">
                        <v-button type="primary"></v-button>
                        <v-button type="default" style="margin-left: 10px;"></v-button>
                    </div>
                    <div class="margin-top-20" style="font-size: 18px;">010-51657777</div>
                </div>
            </div>
            <div slot="footer">
                <v-button key="cancel" type="ghost" size="large" @click="alipayPayDialogCancle2"></v-button>
            </div>
        </v-modal>

        <v-modal
            title=""
            :width="800"
            :visible="alipayPayDialog3"
            @cancel="alipayPayDialogCancle3"
            wrap-class-name="vertical-center-modal">
            <div class="clearfix" style="font-size: 16px;">
                <div class="pull-left margin-left-20">
                    <i class="iconfont icon-warning" style="font-size: 100px;color: #FF4000;"></i>
                </div>
                <div class="pull-left margin-left-20" style="width: 450px;">
                    <div style="font-size: 24px;">
                        
                    </div>
                    <div class="margin-top-20" style="font-size: 14px;">
                        {{payData.name}}
                    </div>
                    <div class="margin-top-20">
                        <v-button type="error"></v-button>
                        <v-button type="default" style="margin-left: 10px;"></v-button>
                    </div>
                    <div class="margin-top-20" style="font-size: 18px;">010-51657777</div>
                </div>
            </div>
            <div slot="footer">
                <v-button key="cancel" type="ghost" size="large" @click="alipayPayDialogCancle3"></v-button>
            </div>
        </v-modal>
    </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      labelCol: {
        span: 2
      },
      wrapperCol: {
        span: 22
      },

      payData: {},

      payStyle: "alipay",

      payState: {
        data: {}
      },

      wechatPayDialog: false,

      yhkPayDialog: false,

      alipayPayDialog1: false,
      alipayPayDialog2: false,
      alipayPayDialog3: false,
      cateList: [],
      payResult: {}
    };
  },
  methods: {
    refreshPrice() {
      //console.log(this.payState);
      this.$czapi
        .refreshPrice({
          token: this.payState.token
        })
        .then(data => {
          this.payData.price = data.data;
          this.$message.success("");
        });

      //
      /*var data = {
                    "code": 200,
                    "msg": "",
                    "data": "100"
                };
                vm.payData.price = data.data;
                vm.$message.success("");*/
    },

    pay() {
      var vm = this;

      this.$czapi
        .pay({
          product_id: this.payData.id,
          channel: this.payStyle
        })
        .then(data => {
          if (data.code !== 200) {
            this.$modal.error({
              title: "",
              content: data.msg
            });
            return false;
          }

          if (vm.payStyle === "wechat") {
            vm.wechatPayDialog = true;
          }
          if (vm.payStyle === "yhk") {
            vm.yhkPayDialog = true;
          }

          this.payResult = data;
          //console.log(data, "pay");
          //this.$nextTick(() => {
        //   if (this.payStyle === "alipay") {
        //     this.alipayPayDialog1 = true;
        //     // vm.alipayPayDialog2 = true;
        //     // vm.alipayPayDialog3 = true;
        //     this.openNew(
        //       `http://aci-api.chaozhiedu.com/api/pay/alipay/${
        //         this.payState.token
        //       }`
        //     );
        //     // let form = $(data.form)[0];
        //     // $("body").append(form);
        //     // form.submit();
        //     // $(form).remove();
        //     //document.forms["alipaysubmit"].submit();
        //   }
          //});
        });

      /*console.log({
                    product_id: vm.payData.id,
                    channel: vm.payStyle
                })*/
    },

    wechatPayDialogCancle() {
      this.wechatPayDialog = false;
    },

    yhkPayDialogCancle() {
      this.yhkPayDialog = false;
    },

    alipayPayDialogCancle1() {
      this.alipayPayDialog1 = false;
    },
    alipayPayDialogCancle2() {
      this.alipayPayDialog2 = false;
    },
    alipayPayDialogCancle3() {
      this.alipayPayDialog3 = false;
    }

    // getCateList() {
    //   var vm = this;
    //   this.$czapi.getCateogry().then(function(data) {
    //     console.log(data);
    //     vm.cateList = data.data;
    //   });
    // }
  },
  mounted() {},
  activated() {
    if (window.payData) {
      this.payData = window.payData;
      this.$czapi
        .getPayInfo({
          product_id: this.payData.id
        })
        .then(data => {
          this.payState = data;
        });
    } else {
      this.$router.push({ name: "Male" });
    }
  }
};
</script>

<style scoped>

</style>



