<template>
    <div class="index">
        <div class="ant-layout">
            <div class="ant-layout ant-layout-has-sider">
                <div class="ant-layout" style="padding: 20px 0;background: #fff;">
                    <div style="width: 1200px;margin: 0 auto;background: #fff;">
                        <div style="margin-bottom: 10px;font-size: 16px;color: #56509F;"></div>
                       
                            <div v-for="it in rows" :key="it.id" class="padding-10 margin-bottom-15" style="border: 1px solid #E8E8E8;border-radius: 6px;">
                                <v-row :gutter="layout.gutter">
                                    <v-col :span="layout.span">
                                        <div style="width: 100%;height: 330px;line-height: 330px;text-align: center; background: #f2f2f2;">
                                            <template v-if="it.img == ''">
                                                <i class="iconfont icon-zanwutupian" style="font-size: 210px;color:#ccc;"></i>
                                            </template>
                                            <template v-else>
                                                <img :src="it.img" alt="" style="height: 330px;">
                                            </template>
                                        </div>
                                    </v-col>
                                    <v-col :span="layout.span">
                                        <div style="font-size: 24px;color: #333333;">{{it.name}}</div>
                                        <div class="margin-top-20">
                                            <span class="inline-block text-right" style="width: 14%;vertical-align: top;"></span>
                                            <span class="inline-block" style="width: 85%;" v-html="it.description"></span>
                                        </div>

                                        <div class="margin-top-20">
                                            <span class="inline-block text-right" style="width: 14%;"></span>
                                            <span class="inline-block" style="width: 85%;">
                                                <span style="font-size: 28px;color: #FE6500;">{{it.price}}</span> 
                                                <span style="text-decoration: line-through;font-size: 20px;margin-left: 20px;">{{it.original_price}}</span>
                                            </span>
                                        </div>

                                        <div class="margin-top-20 text-center">
                                            <a href="javascript:;" class="ant-btn ant-btn-warning"
                                               @click="goPay(it)"
                                               style="font-size: 16px;width: 160px;">
                                                
                                            </a>
                                            <a href="javascript:;" @click="doKF" class="ant-btn"
                                               style="font-size: 16px;width: 160px;margin-left: 20px;">
                                                
                                            </a>
                                        </div>
                                    </v-col>
                                </v-row>
                            </div>
                        <div class="text-right">
                            <v-pagination
                                show-size-changer
                                @change="changePage"
                                @sizechange="pageSizeChange"
                                :total="page.total"
                                show-quick-jumper
                                :show-total="showTotal"></v-pagination>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Index",
  data() {
    return {
      layout: {
        gutter: 32,
        span: 12
      },
      rows: [],
      page: {
        p: 1,
        total: 1,
        offset: 10
      }
      //cateList: []
    };
  },
  computed: {
    ...mapState({
      info: state => state.userInfo
      //cateList: state => state.cateList
    })
  },
  methods: {
    showTotal(total) {
      return ` ${total} `;
    },
    pageSizeChange(current, size) {
      //console.log(current, size);
      this.page.offset = size;
      this.loadData();
    },
    changePage(a) {
      this.page.p = a;
      this.loadData();
    },
    loadData() {
      this.$czapi
        .getProductList({
          p: this.page.p,
          offset: this.page.offset
        })
        .then(({ data }) => {
          this.page.total = data.total;
          this.rows = data.rows;
        });

      /*var data = {
                    "code": 200,
                    "msg": "ok",
                    "data": {
                        "total": 100,
                        "row": [
                            {
                                "id": 1,
                                "name": "AC",
                                "price": "0.10",
                                "original_price": "0.01",
                                "img": "",
                                "description": ""
                            }
                        ]
                    }
                };
                vm.page.total = data.data.total;
                this.info = data.data.row;*/
    },

    goPay(it) {
      window.payData = it;
      this.$router.push({ name: "Pay" });
    },
    doKF(){
        let kfs = $('.doyoo_link.doyoo_online');
        if(kfs.length)
        {
            $(kfs[0]).click();
        }
        else{
            this.$message.info(", 010-51657777");
        }
    }
  },
  mounted() {
    this.loadData();
  },
  activated() {
    let doyoo = document.getElementById("doyoo_panel");
    if(doyoo){
        doyoo.style.display = "block";
    }
  },
  deactivated() {
    let doyoo = document.getElementById("doyoo_panel");
    if(doyoo){
        doyoo.style.display = "none";
    }
  }
};
</script>

<style>

</style>



